<template>
  <div id="bottomPie1"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {
    this.loadChart();
  },

  methods: {
    init(data) {
      var v = [];
      for (var i in data) {
        if (data[i]) {
          v.push({
            name: i,
            value: data[i]
          });
        }
      }
      this.loadChart(v);
    },

    loadChart(data) {
      this.myChart = echarts.init(document.getElementById("bottomPie1"));
      this.myChart.setOption(this.initOption(data));
    },
    initOption(data) {
      return {
        title: {
          text: "航炮",
          subtext: "",
          left: "center",
          bottom: 0,
          textStyle: {
            color: "#00FFFF",
            fontSize: 14
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        color: ["#3be9e7", "#36bcd9", "#2e86cd", "#3560bf"],
        series: [
          {
            name: "标题",
            type: "pie",
            center: ["50%", "45%"],
            radius: ["40%", "65%"],
            clockwise: false,
            labelLine: {
              normal: {
                length: 5
              }
            },
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: true,
                position: "outter",
                formatter: function(parms) {
                  return parms.data.legendname;
                }
              }
            },
            data: data
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
#bottomPie1 {
  width: 100%;
  height: 100%;
}
</style>
