<template>
  <div id="barY"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    init(data) {
      var m = [];
      var w = [];
      for (var i in data) {
        m.push(data[i][0]);
        w.push(data[i][1]);
      }
      for (var i = 0; i < m.length; i++) {
        if (m[i]) {
          m[i] = (m[i] / 1000).toFixed(2);
        }
      }
      for (var i = 0; i < w.length; i++) {
        if (w[i]) {
          w[i] = (w[i] / 1000).toFixed(2);
        }
      }
      this.loadChart(m, w);
    },
    loadChart(m, w) {
      this.myChart = echarts.init(document.getElementById("barY"));
      this.myChart.setOption(this.initOption(m, w));
    },
    initOption(m, w) {
      var m = [m[2],m[1],m[0],m[3]];
      var w = [w[2],w[1],w[0],w[3]];

      return {
        title: {
          text: "发射平均距离",
          left: "center",
          top: 20,
          textStyle: {
            color: "#00FFFF"
          }
        },
        tooltip: {
          trigger: "axis",
          formatter: function(seriesData, ticket, callback) {
            var showHtm = "";
            for (var i = 0; i < seriesData.length; i++) {
              var name = seriesData[i].seriesName;
              var text = seriesData[i].name;
              var value = seriesData[i].value;
              if (i == 0) {
                showHtm = text + "<br>";
              }
              showHtm += name + ": " + value + "km" + "<br>";
            }
            return showHtm;
          },
          axisPointer: {
            type: "shadow"
          }
        },
        legend: {
          bottom: 20,
          data: ["平均命中距离", "平均未命中距离"],
          textStyle: {
            color: "#fff",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "20%",
          containLabel: true
        },
        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,1)"
            }
          },
          axisLabel: {
            color: "#fff",
            fontSize: "16"
          }
        },
        yAxis: {
          type: "category",
          data: ["P-15", "P-12", "P-10", "航炮"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,1)"
            }
          },
          axisLabel: {
            color: "#fff",
            fontSize: "16"
          }
        },
        color: ["#3be9e7", "#ff6a75"],
        series: [
          {
            name: "平均命中距离",
            type: "bar",
            data: m,
            itemStyle: {
              barBorderRadius: 13
            }
          },
          {
            name: "平均未命中距离",
            type: "bar",
            data: w,
            itemStyle: {
              barBorderRadius: 13
            }
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
#barY {
  height: 378px;
}
</style>
