<template>
  <div id="bottomPie2"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    init(data) {
      var v = [];
      for (var i in data) {
        if (data[i]) {
          v.push({
            name: i,
            value: data[i]
          });
        }
      }
      this.loadChart(v);
    },
    loadChart(data) {
      this.myChart = echarts.init(document.getElementById("bottomPie2"));
      this.myChart.setOption(this.initOption(data));
    },
    initOption(data) {
      return {
        title: {
          text: "P-10",
          subtext: "",
          left: "center",
          bottom: 0,
          textStyle: {
            color: "#00FFFF",
            fontSize: 14
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        color: ["#3be9e7", "#36bcd9", "#2e86cd", "#3560bf"],
        series: [
          {
            name: "标题",
            type: "pie",
            center: ["50%", "45%"],
            radius: ["40%", "65%"],
            clockwise: false, //饼图的扇区是否是顺时针排布
            avoidLabelOverlap: false,
            labelLine: {
              normal: {
                length: 5
              }
            },
            label: {
              normal: {
                show: true,
                position: "outter",
                formatter: function(parms) {
                  return parms.data.legendname;
                }
              }
            },
            data: data
            // [
            //   { value: 335, name: "原因1" },
            //   { value: 310, name: "原因2" },
            //   { value: 234, name: "原因3" },
            //   { value: 135, name: "原因4" }
            // ]
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
#bottomPie2 {
  width: 100%;
  height: 100%;
}
</style>
